// export default HomePage;


import React from 'react';
import Head from 'next/head';
import Image from 'next/image';
import Link from 'next/link';

const HomePage: React.FC = () => {
  return (
    <div className="min-h-screen bg-black text-white">
      <Head>
        <title>Classic Refuge Haircut and Shaves</title>
        <link rel="icon" href="/favicon.ico" />
      </Head>

      {/* Navbar */}
      <nav className="flex items-center justify-between px-8 py-4 bg-[#7C6946] shadow-md">
        {/* Logo */}
        <div className="flex items-center space-x-3">
          <Image src="/logo.png" alt="Classic Refuge Logo" width={50} height={50} />
          <span className="text-xl font-semibold tracking-wider">Classic Refuge</span>
        </div>

        {/* Navigation Links */}
        <div className="hidden md:flex space-x-6 text-lg font-medium">
          {['HOME', 'ABOUT', 'BOOKING', 'BARBERS', 'SERVICES'].map((item) => (
            <Link key={item} href={`/${item.toLowerCase()}`}>
              <a className="transition-colors duration-300 hover:text-[#FFD700]">{item}</a>
            </Link>
          ))}
        </div>

        {/* Profile Icon */}
        <div className="relative w-10 h-10 cursor-pointer">
          <Image src="/profile-icon.png" alt="Profile" layout="fill" className="rounded-full" />
        </div>
      </nav>

      {/* Hero Section */}
      <main className="flex flex-col items-center justify-center text-center h-[calc(100vh-80px)] px-6">
        <h1 className="text-6xl md:text-7xl font-extrabold text-[#FFD700] drop-shadow-lg">
          STYLE YOUR HAIR
        </h1>
        <h2 className="text-2xl md:text-3xl mt-3 font-light tracking-wider">
          DEFINE YOUR CONFIDENCE
        </h2>
        <Link 
  href="/booking" 
  className="mt-8 px-8 py-4 text-lg font-medium bg-[#FFD700] text-black rounded-lg shadow-lg hover:bg-[#E5C100] transition-all duration-300"
>
  BOOK NOW
</Link>


      </main>
    </div>
  );
};

export default HomePage;
